












import { Component, Prop, Vue } from 'vue-property-decorator'
import { helpers } from 'chart.js'
import { Point, Sample } from '../../../lib/interfaces'
import ScatterChart from '../chart/ScatterChart'

const RED = '#ff0000'
const BLUE = '#4477dd'
const GRAY = '#999999'

@Component({
  components: {
    ScatterChart
  }
})
export default class DisplayScatter extends Vue {
  @Prop()
  p0!: Point

  @Prop()
  samples!: Sample[]

  titleText = ''
  titleColor = GRAY

  get label () {
    if (!this.samples.length) { return '-' }

    const count = this.samples.length - 1
    const { burnin, result } = this.samples[count]

    return `count: ${count}, ${result} ${burnin ? '[burn-in]' : ''}`
  }

  get labelStyle () {
    if (!this.samples.length) { return { color: '#999999' } }

    const count = this.samples.length - 1
    const { result } = this.samples[count]

    const color = result === 'accepted' ? BLUE : RED

    return { color }
  }

  get datasets (): Chart.ChartDataSets[] {
    const rectPointStyle: Chart.PointStyle = 'rect'
    const first = {
      data: [this.p0],
      fill: false,
      showLine: false,
      lineTension: 0,
      borderWidth: 0,
      borderDash: [0],
      pointStyle: rectPointStyle,
      pointRadius: [5],
      pointBorderWidth: [1],
      pointBackgroundColor: helpers.color(BLUE).alpha(0.72).rgbString(),
      pointBorderColor: helpers.color(BLUE).alpha(0.9).rgbString()
    }

    return [first, ...this.samples.map(({ burnin, current, next, result }) => {
      const rejected = result === 'rejected'

      const pointStyle: Chart.PointStyle = rejected ? 'crossRot' : 'circle'
      const pointRadius = rejected ? [0, 5] : [0, 3]

      const borderWidth = rejected ? [0, 2] : [0, 1]

      const alpha = rejected ? 0.3 : (burnin ? 0.3 : 0.8)
      const baseColor = burnin ? GRAY : (rejected ? RED : BLUE)

      return {
        data: [current, next],
        fill: false,
        showLine: true,
        lineTension: 0,
        borderWidth: 1,
        borderDash: [5, 8],
        pointStyle,
        pointRadius,
        pointBorderWidth: borderWidth,
        pointBackgroundColor: helpers.color(baseColor).alpha(alpha * 0.9).rgbString(),
        pointBorderColor: helpers.color(baseColor).alpha(alpha).rgbString()
      }
    })]
  }
}
