













































import { Component, Vue } from 'vue-property-decorator'

import ControlPanel from './components/ControlPanel.vue'
import DisplayScatter from './components/display/DisplayScatter.vue'
import DisplayHistgram from './components/display/DisplayHistgram.vue'
import DisplayTraceline from './components/display/DisplayTraceline.vue'

import { Point, Sample } from '../lib/interfaces'
import rnorm from '../lib/util/rnorm'
import uniform from '../lib/util/uniform'

/** 提案分布の標準偏差 */
const SIGMA = 1.0

/** 目標分布(二変量正規分布)の共分散 */
const b = 0.5

/** Burn-in 期間の長さ */
const BURN_IN_PERIOD = 50

@Component({
  components: {
    ControlPanel,
    DisplayScatter,
    DisplayHistgram,
    DisplayTraceline
  }
})
export default class App extends Vue {
  /** サンプリングの初期値 x軸の値 */
  private x0 = 3.0

  /** サンプリングの初期値 y軸の値 */
  private y0 = 3.0

  /** 生成したサンプル数 (Burn-in 期間や棄却されたものを含む) */
  private count = 0

  /** 生成したサンプル列 (Burn-in 期間や棄却されたものを含む) */
  private samples: Sample[] = []

  // ======================================================================== //

  /**
   * 提案分布 - 正規分布からの実現値の組
   */
  q ({ x, y }: Point): Point {
    return {
      x: x + rnorm(0, SIGMA),
      y: y + rnorm(0, SIGMA)
    }
  }

  /**
   * 目標分布のカーネル
   */
  p ({ x, y }: Point): number {
    return Math.exp(-0.5 * (x * x - 2 * b * x * y + y * y))
  }

  /**
   * サンプル取得処理
   */
  sample (count: number): Sample {
    const current = this.current || { x: this.x0, y: this.y0 }
    const next = this.q(current)

    const pCurrent = this.p(current)
    const pNext = this.p(next)

    const r = pNext / pCurrent // 受容確率
    const accepted = r > 1 || r > uniform(0, 1)
    const result = accepted ? 'accepted' : 'rejected'

    console.table({
      current: `(${current.x.toFixed(2)}, ${current.y.toFixed(2)})`,
      next: `(${next.x.toFixed(2)}, ${next.y.toFixed(2)})`,
      pCurrent: pCurrent.toFixed(2),
      pNext: pNext.toFixed(2),
      r: r.toFixed(2),
      result
    })

    if (accepted) {
      this.current = { ...next }
    }

    return {
      count,
      burnin: count < BURN_IN_PERIOD,
      current,
      next,
      result
    }
  }

  // ======================================================================== //

  private pause = true
  private speed = 8
  private current?: Point

  get p0 (): Point {
    return {
      x: this.x0,
      y: this.y0
    }
  }

  loop () {
    const sample = this.sample(this.count)
    this.samples.push(sample)
    this.count += 1

    if (!this.pause) { setTimeout(this.loop, 1000 / this.speed) }
  }

  // ======================================================================== //

  onChangePause () {
    if (!this.pause) { this.loop() }
  }

  onReset () {
    this.pause = true
    setTimeout(() => {
      this.count = 0
      this.current = undefined
      this.samples = []
    }, 1000 / this.speed)
  }
}
